.bio {
  flex-wrap: wrap;
  margin: 20px;
  max-width: 100%;
  height: min-content;
  text-align: justify;
  overflow-wrap: break-word;
  z-index: 0;
}

.bio-heading {

  font-family: var(--heading);
  font-weight: var(--bold);

}


.bio-info {
  
  font-family: var(--heading);
  font-weight: var(--regular);

}